@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');

@layer base {
  body {
    @apply font-[Nunito];
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @apply font-[Montserrat] font-bold text-blue text-center;
  }

  h1 {
    @apply text-5xl font-black pt-8;
  }

  p {
    @apply text-xl font-light mb-4;
  }

  li {
    @apply px-4 cursor-pointer;
  }

  button {
    @apply flex items-center border-2 border-cyan px-6 py-2 my-4 text-cyan text-xl rounded-md hover:bg-cyan hover:text-white;
  }

  hr {
    @apply w-[8rem] my-[2rem] mx-[3rem] border-t-4 border-double border-red;
  }
}

.service-img {
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  border: 0.3rem solid white;
}

.contact-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  border-top: 5px solid #4ba3c3;
  padding: 2rem;
  text-align: center;
  border-radius: 1rem;
}

.active {
  color: #ca312f;
}
